import { universalToast } from "../ToastMessages/ToastMessages";

export const checkUser = ({
  firstname,
  lastname,
  phone,
  region,
  district,
  t,
}) => {
  if (firstname.length < 1) {
    universalToast(" !", "warning");
    return false;
  }
  if (lastname.length < 1) {
    universalToast(" !", "warning");
    return false;
  }
  if (phone.length < 9) {
    universalToast("   !", "warning");
    return false;
  }
  if (!region) {
    universalToast(" !", "warning");
    return false;
  }
  // if (!district) {
  //   universalToast(" !", "warning");
  //   return false;
  // }
  return true;
};

export const checkPassword = ({
  password,
  oldPassword,
  confirmPassword,
  t,
}) => {
  if (password.length < 6 || oldPassword.length < 6) {
    universalToast("     6 !", "warning");
    return false;
  }
  if (password !== confirmPassword) {
    universalToast(" !", "warning");
    return false;
  }
  return true;
};

